body {
  margin: 0;
  padding: 0;
  color: var(--clr-black);
  background-color: var(--body-bg);
  scroll-behavior: smooth;
  width: 100%;
  position: relative;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
  &::before {
    @include psevdo;
    width: 100%;
    height: 100%;
    background-image: url('../../img/body-image.png');
    background-repeat: no-repeat;

    background-position: right;
    top: -14%;
    left: 0;
    // transform: translate(-50%, -50%);
    z-index: -1;
  }
}

html {
  box-sizing: border-box;
  width: 100%;
  font-family: var(--ff);
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}


* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  color: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
}

button {
  background-color: transparent;
  border: none;
  display: inline-flex;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: initial;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

input {
  width: 100%;
  outline: none;
  border: none;
}

label {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  position: relative;
  padding: 0 10px;

  @media (min-width: 1440px) and (max-width:1920px) {
    max-width: 1440px;
  }

  &--header {
    max-width: 1620px;

    @media (min-width: 1440px) and (max-width:1920px) {
      max-width: 1440px;
    }
  }
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #fff !important;
    background: #fff !important;
  }
}



